<template lang="">
  <div id="container">
    <h2>player</h2>
    <div class="in_wrap">
      <div class="content" v-for="(p,i) in player" :key="i">
          <h3 @click="modalOpen(i)">{{p.title}}</h3>
          <img @click="modalOpen(i)" :src="p.image" :alt="p.id">
          <Modal v-if="modal" :modal="modal" :player="player"
              :crt="crt" @closeModal="modalClose">
          </Modal>
      </div>
    </div>
  </div>
</template>
<script>
import player from '../assets/player.js'
import Modal from './SoccerModalWrap.vue'
export default {
  data() {
    return {
      player: player,
      modal:false,
      crt:0
    }
  },
  components:{
        Modal
    },
    methods: {
      modalOpen(i) {
        this.crt=i
        this.modal = true
      },
      modalClose(){
        this.modal = false
      }
    },
  
}
</script>
<style>
  #container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: auto;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
</style>