import { createWebHistory, createRouter } from "vue-router";
import Product from './components/ProductWrap.vue'
import Service from './components/ServiceWrap.vue'
import Company from './components/CompanyWrap.vue'
import Soccer from './components/SoccerWrap.vue'

const routes = [
    {
        path: '/',
        component: Product
    },
    {
        path:'/service',
        component: Service
    },
    {
        path:'/company',
        component: Company
    },
    {
        path:'/soccer',
        component: Soccer
    }
]
const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router; 